import { connect } from 'react-redux';
import * as actions from '../actions/';
import Unlock from '../components/Unlock';
import { StoreState } from '../types/index';

import { Dispatch } from 'redux';

// TODO: type props
export function mapStateToProps({credits}: StoreState, ownProps: any) {
  return {
    credits,
    ...ownProps,
  }
}

export function mapDispatchToProps(
    dispatch: Dispatch<actions.CreditAction>, ownProps: any) {
  return {
    purchase: (cost: number) => {
      dispatch(actions.purchase(cost));
      dispatch(ownProps.purchaseAction());
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Unlock);



