// TODO: separte by type
import { CreditAction } from '../actions';
import { 
  BUY_COLORS,
  BUY_CREDITS_PER_SQUARE,
  BUY_PUZZLE_BONUS, 
  BUY_PUZZLE_SIZE,
  BUY_SKIP_COMPLETED,
  BUY_SKIP_UNCHANGED,
  BUY_TICK_SPEED,
  COMPLETE_PUZZLE, 
  COMPLETE_SQUARE, 
  PURCHASE,
} from '../constants/index';
import { StoreState } from '../types/index';

export function credits(state: StoreState, action: CreditAction) {
  switch (action.type) {
    case COMPLETE_SQUARE:
      return {
        ...state,
        credits: state.credits + state.creditsPerSquare * action.amt
      }
    case COMPLETE_PUZZLE:
      return {
        ...state,
        credits: state.credits + (action.size ** state.puzzleBonus),
        puzzlesCompleted: state.puzzlesCompleted + 1,
      }
    case PURCHASE:
      return {...state, credits: state.credits - action.cost }
    case BUY_PUZZLE_BONUS:
      return {...state, puzzleBonus: state.puzzleBonus + 1 }
    case BUY_CREDITS_PER_SQUARE: 
      return {...state, creditsPerSquare: state.creditsPerSquare + 1 }
    case BUY_TICK_SPEED:
      return {...state, tickSpeed: state.tickSpeed * .95 }
    case BUY_PUZZLE_SIZE:
      return {...state, maxPuzzleSize: state.maxPuzzleSize + 5}
    case BUY_SKIP_COMPLETED:
      return {...state, skipCompleted: true}
    case BUY_SKIP_UNCHANGED:
      return {...state, skipUnchanged: true}
    case BUY_COLORS:
      return {...state, colorsUnlocked: true}
    default:
      return state;
  }
}




