import * as React from 'react';
import Spot from '../constants/Spot';
import './Space.css';

export interface Props {
  space: string;
}

class Space extends React.Component<Props, object> {
  constructor(props: Props) {
    super(props);
  }

  public render() {
    return (
      <td className={`space ${this.props.space}`}>
        {this.props.space === Spot.CROSS ? 'X' : '' }
      </td>
    )
  }
}

export default Space;



