import *  as React from 'react';

import { formatMoney } from '../common/utils';

export interface Props {
  name: string;
  credits: number;
  startingCost: number;
  buttonLabel: string;
  costMultiplier: number;
  purchase: (cost: number) => void;
  purchaseAction: () => void;
};

export interface State {
  cost: number;
}

class Upgrade extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      cost: this.props.startingCost,
    };
  }

  public render() {
    return (
      <div>
        <span>Upgrade {this.props.name}</span>
        <button
          disabled={this.props.credits < this.state.cost}
          onClick={this.buy}
        >
          {this.props.buttonLabel} {formatMoney(this.state.cost)}
        </button>
      </div>
    )
  }

  private buy = () => {
    this.props.purchase(this.state.cost);
    this.setState({
      cost: this.state.cost * this.props.costMultiplier
    });
  }
}

export default Upgrade;



