import * as React from 'react';
import Nonogram from '../classes/Nonogram';
import Row from './Row';
import Rule from './Rule';

import ColorNonogram from '../classes/ColorNonogram';

import './Puzzle.css';

export interface Props {
  colorsUnlocked: boolean;
  maxPuzzleSize: number;
  skipCompleted: boolean;
  skipUnchanged: boolean;
  tickSpeed: number;
  onCompletePuzzle: (size: number) => void;
  onCompleteSquare: (amt: number) => void;
};

interface State {
  colors: boolean;
  currentPuzzle: Nonogram;
  intervalId: NodeJS.Timer;
  size: number;
};

class Puzzle extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.setNextPuzzle = this.setNextPuzzle.bind(this);
    const timerId = setTimeout(() => this.tick(), props.tickSpeed);
    this.state = {
      colors: false,
      currentPuzzle: new Nonogram(props.maxPuzzleSize, props.skipCompleted,
        props.skipUnchanged),
      intervalId: timerId,
      size: props.maxPuzzleSize,
    }
  }

  public render() {
    return (
      <div className="puzzle">
        {
          this.state.currentPuzzle.isSolved() ?
          this.renderNextPuzzleButton() :
          this.renderPuzzle()
        }
        <div>Next Puzzle Size:</div>
        <div>
          <span>5</span>
          <input type="range" min={5} max={this.props.maxPuzzleSize} step={5}
            onChange={this.setPuzzleSize} />
          <span>{this.props.maxPuzzleSize}</span>
          <div>{this.state.size}</div>
          {
            this.renderColorSwitch()
          }
        </div>
      </div>
    )
  }

  public componentDidUpdate() {
    if (this.props.skipCompleted) {
      this.state.currentPuzzle.setSkipCompleted();
    }
    if (this.props.skipUnchanged) {
      this.state.currentPuzzle.setSkipUnchanged();
    }
  }

  private renderColorSwitch() {
    if (this.props.colorsUnlocked) {
      return (
        <span>
          <label>Colors: </label>
          <input type="checkbox" onChange={this.colorSwitch} />
        </span>
      );
    }
    return '';
  }

  private renderNextPuzzleButton() {
    return (<button className="button nextPuzzle"
      onClick={this.setNextPuzzle}>Next Puzzle</button>);
  }

  private renderPuzzle() {
    const vertRules = Array(this.state.currentPuzzle.maxRulesHeight).fill(null);
    const emptySquares =
      Array(this.state.currentPuzzle.maxRulesWidth).fill(null);
    const width = Array(this.state.currentPuzzle.size).fill(null);
    const vertIndex = this.state.currentPuzzle.getCurrentVertLine();
    return (
        <div>
        <button className="button abandonPuzzle"
          onClick={this.abandonPuzzle}>Abandon Puzzle</button>
        <div>{this.state.currentPuzzle.name}</div>
        <table className="puzzle">
          <tbody>
          {
            vertRules.map((spot, index) => {
              return (
                <tr key={index}>
                {
                  emptySquares.map((spot2, index2) => {
                    return (<td key={index2}/>);
                  })
                }
                {
                  width.map((spot2, index2) => {
                    return (
                      <Rule 
                        key={index2}
                        rule={
                          this.state.currentPuzzle.getVertRule(index, index2)
                        }
                        active={index2 === vertIndex}
                        onClick={
                          this.ruleClicked.bind(this,
                            index2 + this.state.currentPuzzle.size)
                        }
                      />);
                  })
                }
                </tr>);
            })
          }
          {
            this.state.currentPuzzle.rows.map((row, index) => {
              return (<Row
                key={index}
                maxRulesWidth={this.state.currentPuzzle.maxRulesWidth}
                row={row}
                rules={this.state.currentPuzzle.horizontalRules[index]}
                active={this.state.currentPuzzle.currentLine === index}
                onClick={this.ruleClicked.bind(this, index)}
              />);
            })
          }
          </tbody>
        </table>
        </div>
    )

  }

  private tick() {
    let puzzle;
    if (this.state.currentPuzzle.isSolved()) {
      puzzle = this.getNextPuzzle();
      this.props.onCompletePuzzle(this.state.currentPuzzle.size);
    } else {
      const changed = this.state.currentPuzzle.tick();
      this.props.onCompleteSquare(changed);
      puzzle = this.state.currentPuzzle;
    }
    const intervalId = setTimeout(() => this.tick(), this.props.tickSpeed);
    this.setState({
      currentPuzzle: puzzle,
      intervalId,
    });
  }

  private ruleClicked(line: number) {
    const changed = this.state.currentPuzzle.solveLine(line);
    this.props.onCompleteSquare(changed);
    this.setState({
      currentPuzzle: this.state.currentPuzzle,
    });
  }

  private getNextPuzzle() {
    let puzzle;
    if (this.state.colors) {
      puzzle = new ColorNonogram(this.state.size,
        this.props.skipCompleted, this.props.skipUnchanged);
    } else {
      puzzle = new Nonogram(this.state.size,  
        this.props.skipCompleted, this.props.skipUnchanged);
    }
    return puzzle;
  }

  private abandonPuzzle = () => {
    this.setState({
      currentPuzzle: this.getNextPuzzle(),
    });
  }

  private setNextPuzzle() {
    this.props.onCompletePuzzle(this.state.currentPuzzle.size);
    this.setState({
      currentPuzzle: this.getNextPuzzle(),
    });
  }

  private setPuzzleSize = (evt: React.FormEvent<HTMLInputElement>) => {
    const target = evt.target as HTMLInputElement;
    this.setState({
      size: parseInt(target.value, 10),
    });
  }

  private colorSwitch = (evt: React.FormEvent<HTMLInputElement>) => {
    const target = evt.target as HTMLInputElement;
    this.setState({
      colors: target.checked,
    });
  }
};

export default Puzzle;



