import Spot from '../constants/Spot';
import Row from './Row';

import PUZZLES from '../constants/puzzles';

export default class Nonogram {
  public rows: string[][];
  public horizontalRules: number[][];
  public verticalRules: number[][];
  public name: string;
  // TODO: calculate these on demand
  public maxRulesWidth: number;
  public maxRulesHeight: number;
  public size: number;
  public currentLine: number;
  private skipCompleted: boolean;
  private skipUnchanged: boolean;
  private changedMap: Map<number,boolean>;
  constructor(size: number, skipCompleted: boolean, skipUnchanged: boolean) {
    this.size = size;
    this.skipCompleted = skipCompleted;
    this.skipUnchanged = skipUnchanged;
    this.changedMap = new Map();
    this.rows = Array(this.size).fill(0).map(() => {
      return Array(this.size).fill(Spot.EMPTY);
    });

    const possiblePuzzles = PUZZLES[this.size];
    const puzzle = possiblePuzzles[
      Math.floor(Math.random() * possiblePuzzles.length)];

    this.name = puzzle.name;
    this.horizontalRules = puzzle.horizontal;
    this.verticalRules = puzzle.vertical;

    this.maxRulesWidth = Math.max(...(this.horizontalRules.map(
      (line) => line.length)));
    this.maxRulesHeight = Math.max(...(this.verticalRules.map(
      (line) => line.length)));

    this.currentLine = 0;
  }

  public getVertRule(x: number, y: number) {
    const line = this.verticalRules[y];
    const buffer = this.maxRulesHeight - line.length;
    return line[-1 * (buffer - x)];
  }

  public solveLine(lineNum: number): number{
    const line = this.getLine(lineNum);
    const newLine = this.getRow(lineNum);
    newLine.tick();
    let spotsAdded = 0;
    this.changedMap.set(lineNum, false);
    if (lineNum < this.size) {
      newLine.line.forEach((space, index) => {
        if (line[index] !== space) {
          line[index] = space;
          spotsAdded++;
          this.changedMap.set(index + this.size, true);
        }
      });
    } else {
      const vertLineNum = lineNum % this.size;
      this.rows.forEach((row, index) => {
        if (row[vertLineNum] !== newLine.line[index]) {
          row[vertLineNum] = newLine.line[index];
          spotsAdded++;
          this.changedMap.set(index, true);
        }
      });
    }
    return spotsAdded;
  }

  public tick() {
    const numSolved = this.solveLine(this.currentLine);
    this.getNextLine();
    return numSolved;
  }

  public isSolved() {
    const vertRows = Array(this.size).fill(null).map((num, index) => {
      const line = this.rows.map((row) => {
        return row[index];
      });
      return new Row(line, this.verticalRules[index]);
    });
    const horizontalRows = this.rows.map((row, index) => {
      return new Row(row, this.horizontalRules[index]);
    });
    return vertRows.every(row => row.isSolved()) &&
           horizontalRows.every(row => row.isSolved());
  }

  public getCurrentVertLine() {
    return this.currentLine >= this.size ? this.currentLine % this.size : -1;
  }

  public setSkipCompleted() {
    this.skipCompleted = true;
  }

  public setSkipUnchanged() {
    this.skipUnchanged = true;
  }

  private getRow(lineNum: number): Row {
    let line: string[];
    let rules;
    if (lineNum < this.size) {
      line = this.rows[lineNum];
      rules = this.horizontalRules[lineNum];
    } else {
      const vertLineNum = lineNum % this.size;
      line = this.rows.map((row) => {
        return row[vertLineNum];
      });
      rules = this.verticalRules[vertLineNum];
    }
    return new Row(line.slice(), rules);
  }


  private getLine(lineNum: number): string[] {
    let line: string[];
    if (lineNum < this.size) {
      line = this.rows[lineNum];
    } else {
      const vertLineNum = lineNum % this.size;
      line = this.rows.map((row) => {
        return row[vertLineNum];
      });
    }
    return line;
  }


  private getNextLine(): void {
    this.currentLine++;
    if (this.currentLine >= this.size * 2) {
      this.currentLine = 0;
    }
    if (this.isSolved()) {
      this.currentLine = -1;
      return;
    }
    if (this.skipCompleted && this.getRow(this.currentLine).isSolved()) {
      return this.getNextLine();
    }else if (this.skipUnchanged && this.isRowUnchanged(this.currentLine)) {
      return this.getNextLine();
    }
  }

  private isRowUnchanged(rowNum: number) {
    return this.changedMap.has(rowNum) && !this.changedMap.get(rowNum);
  }

}




