import { connect } from 'react-redux';
import * as actions from '../actions/';
import Puzzle from '../components/Puzzle';
import { StoreState } from '../types/index';

import { Dispatch } from 'redux';

export function mapStateToProps({colorsUnlocked, maxPuzzleSize,
    skipCompleted, skipUnchanged, tickSpeed}: StoreState) {
  return {
    colorsUnlocked,
    maxPuzzleSize,
    skipCompleted,
    skipUnchanged,
    tickSpeed,
  };
}

export function mapDispatchToProps(dispatch: Dispatch<actions.CreditAction>) {
  return {
    onCompletePuzzle: (size: number) => dispatch(actions.completePuzzle(size)),
    onCompleteSquare: (amt: number) => dispatch(actions.completeSquare(amt)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Puzzle);



