import * as React from 'react';

import { formatMoney } from '../common/utils';

export interface Props {
  name: string;
  credits: number;
  cost: number;
  purchase: (cost: number) => void;
  purchaseAction: () => void;
};

export interface State {
  available: boolean;
}

class Unlock extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      available: true,
    };
  }

  public render() {
    if (this.state.available) {
      return (
        <div>
          <span>Unlock {this.props.name}</span>
          <button
            disabled={this.props.credits < this.props.cost}
            onClick={this.buy}
          >
            {formatMoney(this.props.cost)}
          </button>
        </div>
      );
    } else {
      return ('');
    }
  }

  private buy = () => {
    this.props.purchase(this.props.cost);
    this.setState({
      available: false,
    });
  }
}

export default Unlock;



