import * as React from 'react';
import './App.css';

import { Provider } from 'react-redux';
import { createStore } from 'redux';
import { CreditAction } from './actions/index';
import Main from './components/Main';
import { credits } from './reducers/index';
import { StoreState } from './types/index';

const store = createStore<StoreState, CreditAction, null, null>(
  credits, {
  colorsUnlocked: false,
  credits: 0,
  creditsPerSquare: 1,
  maxPuzzleSize: 5,
  puzzleBonus: 0,
  puzzlesCompleted: 0,
  size: 5,
  skipCompleted: false,
  skipUnchanged: false,
  tickSpeed: 10000,
});

class App extends React.Component {
  public render() {
    return (
      <div className="App">
        <Provider store={store}>
          <Main />
        </Provider>
      </div>
    );
  }
}

export default App;



