import * as React from 'react';
import Rule from './Rule';
import Space from './Space';

export interface Props {
  row: string[];
  rules: number[];
  maxRulesWidth: number;
  active: boolean;
  onClick: () => void;
}

class Row extends React.Component<Props, object> {
  constructor(props: Props) {
    super(props);
  }

  public render() {
    const rules = Array(this.props.maxRulesWidth).fill(null);
    return (
      <tr>
      {
        rules.map((spot, index) => {
          return (<Rule key={index} active={this.props.active}
            rule={this.getRule(index)}
            onClick={this.props.onClick}/>);
        })
      }
      {
        this.props.row.map((space, index) => {
          return (<Space key={index} space={space} />);
        })
      }
      </tr>
    )
  }

  private getRule(index: number) {
    const buffer = -1 * (this.props.maxRulesWidth - this.props.rules.length);
    const rule = this.props.rules[index + buffer];
    return rule;
  }

};

export default Row;



