import * as React from 'react';

import * as actions from '../actions/';

import Upgrade from '../containers/Upgrade';

class Upgrades extends React.Component<object, object>{
  public render() {
    return (
      <div className="upgrades">
        <Upgrade name="Tick Speed" startingCost={10} buttonLabel="-5%"
          costMultiplier={1.1} purchaseAction={actions.buyTickSpeed} />
        <Upgrade name="Max Puzzle Size" startingCost={50} buttonLabel="+5"
          costMultiplier={10}  purchaseAction={actions.buyPuzzleSize} />
        <Upgrade name="Credits Per Square" startingCost={100} buttonLabel="+1"
          costMultiplier={2.5} purchaseAction={actions.buyCreditsPerSquare} />
        <Upgrade name="Puzzle Bonus" startingCost={200} buttonLabel="x2"
          costMultiplier={5} purchaseAction={actions.buyPuzzleBonus}/>
      </div>
    )
  }
}

export default Upgrades;



