import * as constants from '../constants';

// TODO: separate this file by action type
export interface CompleteSquare {
  amt: number;
  type: constants.COMPLETE_SQUARE;
}

export interface CompletePuzzle {
  size: number;
  type: constants.COMPLETE_PUZZLE;
}

export interface Purchase {
  type: constants.PURCHASE;
  cost: number;
}

export interface BuyPuzzleBonus {
  type: constants.BUY_PUZZLE_BONUS;
}

export interface BuyCreditsPerSquare {
  type: constants.BUY_CREDITS_PER_SQUARE;
}

export interface BuyTickSpeed {
  type: constants.BUY_TICK_SPEED;
}

export interface BuyPuzzleSize {
  type: constants.BUY_PUZZLE_SIZE;
}

export interface BuySkipCompleted {
  type: constants.BUY_SKIP_COMPLETED;
}

export interface BuySkipUnchanged {
  type: constants.BUY_SKIP_UNCHANGED;
}

export interface BuyColors {
  type: constants.BUY_COLORS;
}

export type CreditAction = CompleteSquare |
  CompletePuzzle |
  Purchase |
  BuyPuzzleBonus |
  BuyCreditsPerSquare |
  BuyTickSpeed |
  BuyPuzzleSize |
  BuySkipCompleted |
  BuySkipUnchanged |
  BuyColors;

export function completeSquare(amt: number): CompleteSquare {
  return {
    amt,
    type: constants.COMPLETE_SQUARE,
  }
}

export function completePuzzle(size: number): CompletePuzzle {
  return {
    size,
    type: constants.COMPLETE_PUZZLE,
  }
}

export function purchase(cost: number): Purchase {
  return {
    cost,
    type: constants.PURCHASE,
  }
}

export function buyPuzzleBonus(): BuyPuzzleBonus {
  return {
    type: constants.BUY_PUZZLE_BONUS,
  }
}

export function buyCreditsPerSquare(): BuyCreditsPerSquare {
  return {
    type: constants.BUY_CREDITS_PER_SQUARE,
  }
}

export function buyTickSpeed(): BuyTickSpeed {
  return {
    type: constants.BUY_TICK_SPEED,
  }
}

export function buyPuzzleSize(): BuyPuzzleSize {
  return {
    type: constants.BUY_PUZZLE_SIZE,
  }
}

export function buySkipCompleted(): BuySkipCompleted {
  return {
    type: constants.BUY_SKIP_COMPLETED,
  }
}

export function buySkipUnchanged(): BuySkipUnchanged {
  return {
    type: constants.BUY_SKIP_UNCHANGED,
  }
}

export function buyColors(): BuyColors {
  return {
    type: constants.BUY_COLORS,
  }
}



