import * as React from 'react';

import * as actions from '../actions/';

import Unlock from '../containers/Unlock';

class Unlocks extends React.Component<object, object>{
  public render() {
    return (
      <div className="unlocks">
        <Unlock name="Skip Completed Rows" cost={50}
          purchaseAction={actions.buySkipCompleted} />
        <Unlock name="Skip Unchanged Rows" cost={500}
          purchaseAction={actions.buySkipUnchanged} />
        <Unlock name="Colors" cost={5000}
          purchaseAction={actions.buyColors} />
      </div>
    )
  }
}

export default Unlocks;



