import * as React from 'react';

import { formatMoney, formatNumber } from '../common/utils';

export interface Props {
  credits: number;
  puzzlesCompleted: number;
  puzzleBonus: number;
  maxPuzzleSize: number;
  creditsPerSquare: number;
  tickSpeed: number;
};

class Stats extends React.Component<Props, object>{
  public render() {
    const { 
      credits,
      creditsPerSquare, 
      maxPuzzleSize,
      puzzleBonus,
      puzzlesCompleted,
      tickSpeed,
    } = this.props;
    return (
      <div className="stats">
        <div>Credits: {formatMoney(credits)}</div>
        <div>Tick Speed: {formatNumber(tickSpeed)}</div>
        <div>Max Puzzle Size: {maxPuzzleSize}</div>
        <div>Puzzles Completed: {puzzlesCompleted}</div>
        <div>Credits Per Square: {creditsPerSquare}</div>
        <div>Puzzle Bonus: {puzzleBonus}</div>
      </div>
    )
  }
};

export default Stats;



