import * as React from 'react';

import './Rule.css';

export interface Props {
  rule?: number;
  active: boolean;
  onClick: () => void;
}

class Rule extends React.Component<Props, object> {
  constructor(props: Props) {
    super(props);
  }

  public render() {
    return <td
      className={"rule" + (this.props.active ? " active" : "")}
      onClick={this.props.onClick}
    >{this.props.rule ? this.props.rule : ' '}</td>;
  }
}

export default Rule;



