import Spot from '../constants/Spot';

export default class Row {
  public line: string[];
  private rules: number[];
  constructor(line: string[], rules: number[]) {
    this.line = line;
    this.rules = rules;
  }

  public tick() {
    this.solve();
  }

  public isSolved() {
    if (this.line.includes(Spot.EMPTY)) {
      return false;
    }
    const curCount = this.getCurrentCount();
    if (curCount.length !== this.rules.length) {
      return false;
    }
    return curCount.every((spot, index) => {
      return spot === this.rules[index];
    });
  }

  private solve() {
    this.line.forEach((space, index) => {
      if (space === Spot.EMPTY) {
        const newSpaces = this.line.slice();
        newSpaces[index] = Spot.FILL;
        let testLine = new Row(newSpaces, this.rules);
        if (!testLine.isPossible()) {
          this.line[index] = Spot.CROSS;
          return;
        }
        newSpaces[index] = Spot.CROSS;
        testLine = new Row(newSpaces, this.rules);
        if (!testLine.isPossible()) {
          this.line[index] = Spot.FILL;
          return;
        }
      }
    });
  }

  private isPossible(): boolean {
    if (this.rules.length === 0) {
      return !this.line.includes(Spot.FILL);
    }
    if (this.isSolved()) {
      return true;
    }
    if (!this.line.includes(Spot.EMPTY)) {
      return false;
    }
    const totalBlocks = this.line.filter(space => space === Spot.FILL).length;
    const totalRules = this.rules.reduce((x, y) => x + y);
    const totalSpaces = this.line.filter(space => space === Spot.EMPTY).length;
    if (totalBlocks > totalRules) {
      return false;
    }
    if (totalRules > totalBlocks + totalSpaces) {
      return false;
    }
    const verifiedCount = this.getVerifiedCount();
    let curIndex = 0;
    const canExist = verifiedCount.map((count) => {
      curIndex = this.rules.indexOf(count, curIndex);
      return curIndex !== -1;
    });
    if (canExist.includes(false)) {
      return false;
    }
    const newSpaces = this.line.slice();
    const index = newSpaces.indexOf(Spot.EMPTY);
    newSpaces[index] = Spot.FILL;
    let newLine = new Row(newSpaces, this.rules);
    if (newLine.isPossible()) {
      return true;
    }
    newSpaces[index] = Spot.CROSS;
    newLine = new Row(newSpaces, this.rules);
    return newLine.isPossible();
  }

  private getCurrentCount() {
    const count = [];
    let currentlyPart = false;
    let curLength = 0;
    this.line.forEach((space) => {
      if (space === Spot.FILL) {
        curLength++;
        currentlyPart = true;
      } else if (currentlyPart) {
        currentlyPart = false;
        count.push(curLength);
        curLength = 0;
      }
    });
    if (curLength > 0) {
      count.push(curLength);
    }
    return count;
  }

  private getVerifiedCount() {
    const verifiedCount = [];
    let i = 0;
    while (i < this.line.length) {
      if (this.line[i] === Spot.CROSS) {
        i++;
      } else if (this.line[i] === Spot.EMPTY) {
        while (this.line[i] === Spot.EMPTY || this.line[i] === Spot.FILL) {
          i++;
          if (i === this.line.length) {
            break;
          }
        }
      } else if (this.line[i] === Spot.FILL) {
        let j = 1;
        while (i + j <= this.line.length) {
          if (i + j === this.line.length) {
            verifiedCount.push(j);
            break;
          } else if (this.line[i+j] === Spot.CROSS) {
            verifiedCount.push(j);
            break;
          } else if (this.line[i+j] === Spot.EMPTY) {
            break;
          } else if (this.line[i+j] === Spot.FILL) {
            j++;
          }
        }
        i += j;
      }
    }
    return verifiedCount;
  }
}



